@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
.header{position:relative;background-color:#fff;height:4rem;display:flex;padding-top:20px;color:#ff5200}.header h1{margin:0;width:auto;line-height:30px;width:100px;display:block;padding-left:2%}.main-img{overflow:hidden;height:25rem}.main-img img{width:110%;height:110%;transform:translateX(-20px) translateY(-8rem) rotate(-4deg)}.mealsSummary{color:#fff;width:40%;height:15rem;position:relative;background-color:#292929;border:1px #292929 solid;text-align:center;box-shadow:5px 10px 25px #000;margin:2% 30%;margin-top:-14rem;padding:10px;border-radius:20px;z-index:2}@media(max-width: 768px){.mealsSummary{margin:auto;margin-bottom:16%;width:90%}}.meals{text-align:-webkit-center}.card{padding:10px 20px;background-color:#fff;box-shadow:0 2px 8px #fff;border-radius:14px;width:50%}.card li{list-style-type:none}@media(max-width: 768px){.card{width:90%}}.mealItem{border-bottom:1px solid gray;padding:30px 0;text-align:left;line-height:28px}@media(max-width: 768px){.mealItem{display:flex !important;flex-direction:column !important}}.mealItem .name{font-weight:bold}.mealItem .description{font-style:italic;font-family:cursive}.mealItem .price{color:#ff5200;font-weight:bold}.form button{background-color:#ff5200;margin:9px;margin-top:7px;border-radius:25px;width:30%}.form button:hover{color:#ff5200;background-color:#fff}.cartClass{max-height:50rem;overflow:scroll}.cartClass .total{padding:10px;padding-right:5px;font-size:x-large;font-weight:bold}.cartClass .total span:first-child{padding-right:5px}.cartClass .total span:nth-child(2){float:right;padding-right:20px}.cartClass .cart-items .itemDiv{padding:10px;padding-right:5px;font-size:x-large;font-weight:bold;display:flex;width:-webkit-fill-available}.cartClass .cart-items .itemDiv .buttons{display:inherit}.cartClass .cart-items .itemDiv .buttons button{margin:10px;font-size:x-large;font-weight:bold;border:1px solid #ff5200;border-radius:25px;color:#fff;height:40px;width:60px;background:#ff5200}.action button{background-color:#ff5200;margin:9px;margin-top:7px;border-radius:5px;width:20%;height:33px;border:none}.action button:hover{color:#ff5200;background-color:#fff}.content{padding:10px 20px;background-color:#fff;box-shadow:0 2px 8px #fff;border-radius:14px;width:50%;position:fixed;top:30vh;left:25%;z-index:4}.backdrop{position:fixed;top:0;width:100%;height:100vh;background:rgba(0,0,0,.75);z-index:3}li{list-style-type:none}.cart-button{display:block;color:#fff;background-color:#ff5200;border:1px solid #fff;border-radius:25px;height:3rem;width:5rem;right:3%;position:absolute;text-align:center;padding:10px 0 0 18px;display:inline-flex;top:16%;cursor:pointer}.cartNumber{font-size:22px}.bump{animation:bump 300ms ease-out;color:#000;background-color:#700a0a}@keyframes bump{0%{transform:scale(1)}10%{transform:scale(0.9)}30%{transform:scale(1.1)}50%{transform:scale(1.15)}100%{transform:scale(1)}}
